* {
  margin: 0;
  padding: 0;
  outline: none;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", sans-serif;
  width: 100%;
}

html, body {
  height: 100%;
}

h1 {
  line-height: 1.5;
}
